.app {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.gameboard {
    width: 300px;
    height: 300px;
    display: flex;
    flex-wrap: wrap;
    border: solid 1px black;

}
.square {
    width: 100px;
    height: 100px;
    border: solid 2px black;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}
.circle {
    height: 90px;
    width: 90px;
    border-radius:50%;
    border: 15px solid blue;
    box-sizing: border-box;

}
.cross {
    width: 90px;
    height: 90px;
    position: relative;
    transform: rotate(45deg);
}
.cross:before, .cross:after {
    content: "";
    position: absolute;
    background-color: red;

}

.cross:before {
    left: 50%;
    width: 30%;
    height: 100%;
    margin-left: -15%;
}

.cross:after {
    top:50%;
    width: 100%;
    height: 30%;
    margin-top: -15%;
}